@import url(//fonts.googleapis.com/css?family=Open+Sans);

body {
  font-family: 'Open Sans';
  color: red;
}

:root {
  --primary-color: #45A2F8;
  --secondary-color: #838383;
  --warning-color: #dca100;
  --danger-color: #cb1a1a;
  --success-color: #28a745;
}

.primary-color {
  color: var(--primary-color) !important;
}

.secondary-color {
  color: var(--secondary-color) !important;
}

.secondary-color-darker {
  color: #5A5A5AFF;
}

.warning-color {
  color: var(--warning-color) !important;
}

.danger-color {
  color: var(--danger-color) !important;
}

.data{
  font-weight: 500;
  align-items: center;
  letter-spacing: -0.462963px;
  color: var(--primary-color) !important;
}

.title {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 38px;
  /* identical to box height, or 160% */
  /* Global/08. Dark */
  color: #343A40;
}

.subtitle{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  /* identical to box height, or 150% */
  color: #848692;

}

.stato {
  padding: 3px 4px;
  width: 163px;
  height: 18px;
 color: var(--primary-color) !important;;
  background: #EFF2F5;
  border-radius: 4px;
  font-weight: 600;
}

.button-disabled {
  color: var(--secondary-color) !important;
}

.pagination-button button{
  color: var(--primary-color);
  background: #fff;
  padding: .3rem .5rem;
  border: #e3e3e3 solid 1px;
  text-align: end;
}

.pagination-button-disabled {
  color: var(--secondary-color) !important;
  background: transparent !important;
  border: none !important;
}



.table-head {
  border-style: hidden !important;
}
.table-head th{
  color: #7F8297;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
}

.table-row:hover {
  background-color: rgba(0, 163, 240, 0.2);
}


*,*:focus,*:hover{
  outline:none;
}


.white-rounded-shadowed {
  background-color: #fff;
  box-shadow: #d1d1d1 0 4px 3px 1px;
  padding: 3rem;
  margin: 1rem;
}

.pagination{
  margin-right: 24px;
}
.back-col {
  background-color: #E5E5E5;
  padding-bottom: 5rem;
  padding-top: 1rem;
  padding-left: 5rem;
  padding-right: 5rem;
}
